import * as React from "react"
import {SEO} from "../components/seo"
import {Canvas} from "@react-three/fiber";
import {OrbitControls} from "@react-three/drei";
import Trophy from "../components/3DModels/Trophy";
import Navigation from "../components/layout/Navigation";
import {useEffect} from "react";

const ThankYou = () => {
    require("../scss/thank_you.scss");
    useEffect(() => {
        setTimeout(() => {
            window.location.replace('https://kalmars.lv/');
        }, 10000)
    }, [])
    return (
        <main className="main-wrapper">
            <Navigation pageNr={'U77KL'}/>
            <div id="thank-you" className="md:container mx-auto min-h-screen flex flex-col justify-center items-center relative px-4">
                <div className="w-full css--thank-you-canvas">
                    <div style={{width: "100%", height: "100%", position: "relative"}}>
                        <Canvas
                            gl={{alpha: true}}
                            camera={{position: [0, 0, 4], fov: 40}}>
                            <OrbitControls makeDefault autoRotate={false} enablePan={false} enableZoom={false} autoRotateSpeed={0.5} zoomSpeed={0.1}/>
                            <directionalLight intensity={1} position={[2, 2, 0]} color="#2fc7e5" distance={5}/>
                            <React.Suspense fallback={null}>
                                <Trophy/>
                            </React.Suspense>
                        </Canvas>
                    </div>
                </div>
                <div className="w-full">
                    <h1 className="text-3xl md:text-5xl glitch text-center mb-0" data-glitch="Thank You!">
                        <span className="title-underline">
                            Thank You!
                        </span>
                    </h1>
                </div>
                <div className="text-lg md:text-xl text-white text-center mb-0">
                    You will be redirected shortly
                </div>
            </div>
        </main>
    )
}
export default ThankYou;
export const Head = () => <SEO title={"Thank You"}/>

